let calc = document.querySelectorAll(".cartcalc")


function cartCalc() {
    calc.forEach(el => {
        let calcMinus = el.querySelector(".ccalc-minus")
        let calcPlus = el.querySelector(".ccalc-plus")
        let input = el.querySelector("input")

        let isMainCalc = el.classList.contains("_main")

        calcMinus.onclick = function() {
            let a = input.value;
            if (a > 1) {
                let b = +a - 1;
                input.value = b;
                if(isMainCalc){
                    $('.header-cart__count span').html(
                        $(".cart .cartcalc input").val()
                    )
                }
            } else {
                input.value = a;
                if(isMainCalc){
                    $(".cart-nav").removeClass("_added")
                    $('.header-cart').removeClass("_active")
                    $('.header-cart__count span').html(0)
                }
            }
        }

        calcPlus.onclick = function(e) {
            let a = input.value;
            let b = +a + 1;
            input.value = b;

            if(isMainCalc){
                cartAnim(e)
            }
        }
    });
}

cartCalc()

$(".cart-nav__btn").click(function (e) {
    $(".cart-nav").addClass("_added")
    cartAnim(e)
})

function cartAnim(e) {
    let butWrap = document.querySelector(".cart .cart-nav");
    let headerCart = document.querySelector(".header-cart");
    console.log(e.pageX)
    console.log(butWrap.getBoundingClientRect())

    $(".cart-nav").append('' +
        `<div class="animtocart">
            <img src="${$('.order__img img').attr('src')}" alt="">
        </div>`
    );
    $('.animtocart').css({
        'position' : 'fixed',
        'z-index' : '9999999999',
        'left' : butWrap.getBoundingClientRect().x + 'px',
        'top' : butWrap.getBoundingClientRect().y + 'px',
    });
    $('.animtocart').animate({
        top: "1em",
        left: headerCart.getBoundingClientRect().x + 'px',
        opacity: 0,
        // width: 0,
        // height: 0
    }, 800, function(){
        $('.header-cart').addClass("_active")
        $('.header-cart__count span').html(
            $(".cart .cartcalc input").val()
        )
        $(this).remove();
    });
}

function setLabelPlace(){
    let fields = document.querySelectorAll(".fg input,.fg textarea,.fg-dynamic select")

    fields.forEach(field => {
        setLabelPosition(field)

        field.onchange = ()=> {
            setLabelPosition(field)
        }

        function setLabelPosition() {
            if(field.value !== ""){
                field.classList.add("_filled")
            } else{
                field.classList.remove("_filled")
            }
        }
    })
}


setLabelPlace()
