$(".address-field__variant").click(function () {

})

$(".address-field").each(function (i,el) {
    let input = $(el).find("input")

    $(input).on("input",function () {
        if($(this).val().length > 0){
            $(el).addClass("_show-result")
        } else{
            $(el).removeClass("_show-result")
        }
    })

    $(el).find(".address-field__variant").click(function () {
        let value = $(this).attr("data-value")
        input.val(value)
        $(el).removeClass("_show-result")
        $(input).val("").trigger("change");
    })

    $(document).mouseup( function(e){
        let div = $(el);
        if ( !div.is(e.target)
            && div.has(e.target).length === 0 ) {
            if($(el).hasClass("_show-result")){
                $(el).removeClass("_show-result")
            }
        }
    });
})


$('.delivery-item input').change(function () {
    let value = $(this).attr("data-value")
    $("._delivery-type-fields").removeClass("_active")
    $(`._delivery-type-fields.${value}`).addClass("_active")
})

$("[data-cart]").click(function () {
    let modalId = $(this).attr("data-cart")
    let modal = $(`#${modalId}`)

    $(modal).addClass("_active")
    $("body").addClass("fixed-body")
})

$("._close-cart").click(function () {
    $(".cart-modal").removeClass("_active")
    $("body").removeClass("fixed-body")
})
